@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;600;700&display=swap);
* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-shadow: border-box;
}

:root {
  --bg: #abd1c6;
  --bg-dark: #004643;
  --bg-headline: #fffffe;
  --bg-paragraph: #133a2f;
  --bg-buttonTest: #001e1d;
  --bg-button: #f9bc60;
}

html,
body {
  background-color: #004643;
  background-color: var(--bg-dark);
}

a {
  text-decoration: none;
}

a {
  color: black;
}

.heading {
  margin: 20px 0;
}

.heading h1 {
  color: var(--bg-headline);
}

.heading p {
  color: var(--bg);
}

footer p {
  color: var(--bg);
  margin: 20px;
}

.main {
  text-align: center;
}

.main h1 {
  font-size: 20px;
  text-transform: uppercase;
}

.container {
  width: 1000px;
  margin: 0 auto;
}

.item_container {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 20px;
  justify-items: center;
}

.item {
  width: 100%;
  height: 100px;
  box-shadow: 2px 2px 2px black;
  background-color: whitesmoke;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.disabled {
  background-color: rgb(104, 104, 104) !important;
  width: 100%;
  height: 100px;
  box-shadow: 2px 2px 2px black;
  border-radius: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.item:hover,
.project_item:hover {
  cursor: pointer;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.item a {
  width: 60%;
}

/* PROJECT CONTAINER */

.projectContainer {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px;
  justify-items: center;
  margin-bottom: 40px;
}

.project_item {
  width: 100%;
  height: 100%;
  box-shadow: 2px 2px 2px black;
  background-color: whitesmoke;
  border-radius: 5px;
  color: var(--bg-dark);
  padding: 10px;

  display: -webkit-flex;

  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.project_description {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.project_item img {
  width: 50px;
  border-radius: 0px;
  box-shadow: none;
  margin: 0px;
  padding-bottom: 5px;
}

.project_item h3 {
  text-transform: uppercase;
}

.project_item p {
  font-size: 14px;
  padding: 5px 10px;
}

.middle {
  /* text-align: center;
  display: flex;
  justify-content: center;
  align-items: center; */
  text-align: center;
  width: 60%;
  margin: 0 auto;
  color: white;
}

/* BUTTON */

button {
  border: none;
  padding: 10px 20px;
  color: var(--bg-dark);
  background-color: var(--bg);
  margin: 10px;
  box-shadow: 5px 5px 2px black;
}

button:focus {
  outline: none;
}

button:hover {
  cursor: pointer;
}

button:active {
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}

.component_image {
  width: 30rem;
  border-radius: 10px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.801);
  margin: 20px;
}

.codesyntax {
  text-align: left;
  box-shadow: 5px 5px 2px black;
  padding: 30px !important;
  margin: 20px 0 40px 0;
  border-radius: 10px;
}

.navigation {
  margin-bottom: 20px;
}

.wrapper p {
  color: white;
}

@media only screen and (max-width: 1024px) {
  .container {
    width: 90%;
  }

  .item_container {
    grid-template-columns: repeat(2, 1fr);
  }

  .item {
    width: 100%;
  }

  .projectContainer {
    grid-template-columns: repeat(1, 1fr);
  }

  .component_image {
    width: 90% !important;
  }
}

@media only screen and (max-width: 540px) {
  .middle {
    text-align: center;
    width: 100%;
    margin: 0 auto;
    color: white;
  }
}

.state {
  text-align: center;
  color: var(--bg-headline);
}

.state p {
  margin: 20px;
}

.props_box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.props_box p {
  color: var(--bg-headline);
}

.props_box button {
  background-color: var(--bg-button);
}


.form {
  padding: 30px;
  background-color: var(--bg-buttonTest);
  border-radius: 5px;
  margin-top: 20px;
  box-shadow: 5px 5px 2px black;
}

.form input {
  padding: 10px;
  width: 340px;
  background-color: rgb(231, 231, 231);
  border: none;
  border-radius: 5px;
}

@media only screen and (max-width: 540px) {
  .form input {
    width: auto;
  }

  .form {
    margin-bottom: 20px;
  }
}

.listAndKey {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.listAndKey li {
  text-decoration: none;
  list-style: none;
  color: var(--bg-headline);
  margin: 20px;
}

.data {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.data_each {
  background-color: var(--bg-headline);
  width: 150px;
}

h2 {
  color: white;
  margin: 20px;
}

p {
  color: black;
}

.data {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
span {
  color: white;
}

.data_each {
  background-color: var(--bg-headline);
  margin: 10px;
  border-radius: 10px;
}

.container {
  color: white;
}

p {
  margin: 10px 0;
}

.box {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.wrapper p {
  color: white;
}

.customhook {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 10px;
}

.customhook input:first-child {
  padding: 10px;
}

.customhook input {
  margin: 10px;
}

